import React from 'react';
import { ConfigProvider } from 'antd';
import styles from './Tasks.module.css';
import { TonConnectUI } from '@tonconnect/ui';

const Tasks = () => (
  <ConfigProvider
    theme={{
      token: {
        // Seed Token
        colorPrimary: '#494949',
        borderRadius: 10,
      },
    }}
  >
    <div className={styles.wallet}>
      <h1 className={styles.beta}>Available Tasks:</h1>
      <h1 className={styles.beta2}>Tasks will be shown soon...</h1>
      {/* Add your wallet page content here */}
    </div>
  </ConfigProvider>
);

export default Tasks;