import React, { useEffect } from 'react';
import { Button, ConfigProvider } from 'antd';
import { HomeOutlined, SearchOutlined, WalletOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import styles from './Footer.module.css';

const Footer: React.FC = () => {
  const navigate = useNavigate();

  const handleButtonClick = (buttonName: string) => {
    if (buttonName === 'tasks') {
      navigate('/tasks');
    }

    if (buttonName === 'home') {
      navigate('/');
    }

    if (buttonName === 'wallet') {
      navigate('/wallet');
    }
  };

  useEffect(() => {
    handleButtonClick('home');
  }, []);

  return (
    <ConfigProvider
      theme={{
        token: {
          // Seed Token
          colorPrimary: '#606060',
          borderRadius: 10,
        },
      }}
    >
      <div className={styles.footer}>
        <Button type="primary" icon={<HomeOutlined />} className={styles.button} onClick={() => handleButtonClick('home')}>
          Home
        </Button>
        <Button type="primary" icon={<SearchOutlined />} className={styles.button} onClick={() => handleButtonClick('tasks')}>
          Tasks
        </Button>
        <Button type="primary" icon={<WalletOutlined />} className={styles.button} onClick={() => handleButtonClick('wallet')}>
          Wallet
        </Button>
      </div>
    </ConfigProvider>
  );
};

export default Footer;