import React, {useCallback, useState} from 'react';
import { ConfigProvider } from 'antd';
import styles from './Wallet.module.css';
import './Wallet.module.css';
import {SendTransactionRequest, useTonAddress, useTonConnectUI, useTonWallet} from "@tonconnect/ui-react";
import { toNano } from 'ton';

const Wallet = () => (
  <ConfigProvider
    theme={{
      token: {
        // Seed Token
        colorPrimary: '#494949',
        borderRadius: 10,
      },
    }}
  >
    <div className={styles.wallet}>
      <h1 className={styles.beta}>Connect your Wallet:</h1>
      <h1 className={styles.beta2}>Will be available soon...</h1>
      {/* Add your wallet page content here */}
    </div>
  </ConfigProvider>
);

export default Wallet;